
#app{font-family:'Microsoft Yahei', 'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#2c3e50
}
.tabs{display:-ms-flexbox;display:flex;width:100%;height:40px;line-height:40px;margin:0;position:relative
}
.tabs::after{content:"";display:block;position:absolute;left:0;bottom:0;width:100%;border-top:1px solid rgba(7,17,27,0.1)
}
.tabs__tab-item{display:block;-ms-flex:1;flex:1;text-align:center;font-size:14px;color:#4d555d
}
.tab-active{color:red
}



