
.seller{background-color:#f3f5f7;overflow:hidden
}
.seller__header{background-color:#fff;border-bottom:1px solid rgba(7,17,27,0.1);margin-bottom:18px
}
.seller__header_title{position:relative;margin:0 18px;padding:18px 0;text-align:left;border-bottom:1px solid rgba(7,17,27,0.1)
}
.seller__header_title_name{font-size:14px;color:#07111b;line-height:14px;margin-bottom:8px
}
.seller__header_title_score{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-align:center;align-items:center;font-size:10px;color:#4d555d
}
.stars{-ms-flex-pack:justify;justify-content:space-between;margin-right:8px
}
.seller__header_title_score_count{margin-right:12px
}
.seller__header_title_heart{position:absolute;top:18px;right:18px;text-align:center;margin:auto;width:50px
}
.icon-favorite{font-size:24px;line-height:24px;color:rgba(7,17,27,0.2)
}
.icon-favorite.collected{color:#f01414
}
.seller__header_title_heart_text{margin-top:4px;font-size:10px;color:#4d555d;line-height:10px
}
.seller__header_info{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
.seller__header_info_item{-ms-flex:1;flex:1;margin:18px 0;border-right:1px solid rgba(7,17,27,0.1)
}
.seller__header_info_item_title{font-size:10px;color:#07111b;line-height:10px;margin-bottom:4px
}
.seller__header_info_item_num{font-size:24px;font-weight:200;color:#07111b;line-height:24px
}
.seller__header_info_item_num .unit{font-size:10px
}
.seller__header_info_item:last-child{border-right:none
}
.seller__notice{background-color:#fff;padding:18px 18px 0;margin-bottom:18px;text-align:left
}
.seller__notice_bulletin_title{margin:0;margin-bottom:8px;font-size:14px;color:#07111b;line-height:14px;font-weight:normal
}
.seller__notice_bulletin_text{padding:0 12px 12px;margin:0;font-size:12px;font-weight:200;color:#f01414;line-height:24px
}
.seller__notice_supports{margin:0;padding:0
}
.seller__notice_supports .supports_support{border-bottom:1px solid rgba(7,17,27,0.1);height:48px;font-size:12px;font-weight:200;color:#07111b;line-height:16px
}
.seller__notice_supports .supports_support:first-child{border-top:1px solid rgba(7,17,27,0.1)
}
.seller__notice_supports .supports_support img{width:16px;height:16px;margin-left:12px;margin-right:6px;background-size:contain
}
.seller__site{padding:18px;background-color:#fff;text-align:left;margin-bottom:18px
}
.seller__site_title{font-size:14px;color:#07111b;line-height:14px;margin-bottom:12px;font-weight:normal
}
.seller__site_images{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;list-style-type:none;margin:0;padding:0
}
.seller__site_images_image img{width:120px;height:90px;margin-right:6px
}
.seller__info{text-align:left;padding:18px;background-color:#fff
}
.seller__info_title{font-size:14px;color:#07111b;line-height:14px;margin-bottom:12px;font-weight:normal
}
.seller__info_text{margin:0;padding:0;list-style-type:none
}
.seller__info_text_item{padding:16px 12px;border-bottom:1px solid rgba(7,17,27,0.1);font-size:12px;font-weight:200;color:#07111b;line-height:16px
}
.seller__info_text_item:first-child{border-top:1px solid rgba(7,17,27,0.1)
}
.seller-wrapper{position:absolute;top:178px;right:0;bottom:0;left:0;overflow:hidden;width:100%
}
.images-wrapper{width:100%;overflow:hidden;white-space:nowrap
}



